/* src/styles/Loading.css */
.loading {
  position: fixed;       /* 固定在页面上 */
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;         /* 高度占满整个视口 */
  display: flex;
  justify-content: center; /* 水平居中 */
  align-items: center;     /* 垂直居中 */
  background-color: rgba(255, 255, 255, 0.8); /* 半透明背景，可选 */
  font-size: 18px;
  font-weight: bold;
  z-index: 9999;          /* 确保在最上层 */
}
